<template>
  <div class="login-view">
    <el-row style="height: 100%;">
      <el-col :xs="20" :sm="10" :md="8" :lg="8" class="login-form">
        <el-card class="box-card">
          <div slot="header">
            <span style="font-size: 16px">LOG IN</span>
          </div>
          <el-form label-position="top"  @keyup.enter.native="login">
            <el-form-item label="Username">
              <el-input
                v-model="username"
                v-bind:class="{ error: loginError.username }"
                v-bind:placeholder="loginError.username"
                @focus="loginError.username=''"
              ></el-input>
            </el-form-item>
            <el-form-item label="Pasword">
              <el-input type="password"
                v-model="password"
                v-bind:class="{ error: loginError.password }"
                v-bind:placeholder="loginError.password"
                @focus="loginError.password=''"
              ></el-input>
            </el-form-item>
          </el-form>
          <div class="login-footer">
            <div class="checkbox">
              <el-checkbox v-model="remember" style="color: inherit;">Remember</el-checkbox>
            </div>
            <el-button type="success" @click="login">Log In</el-button>
          </div>
          <!--<div>-->
            <!--Not a member?-->
            <!--&lt;!&ndash;<a href="javascript:;" @click="signup"> Sign up now</a>&ndash;&gt;-->
          <!--</div>-->
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>


<script>
import axios from 'axios'
import { Col, Row, Card, Form, FormItem, Input, Checkbox, Button } from 'element-ui'
import { mapActions } from 'vuex'
import { USER_LOGIN } from '../store/mutation-types'


export default {
  name: 'login-view',
  components: {
    'el-col': Col,
    'el-row': Row,
    'el-card': Card,
    'el-form': Form,
    'el-form-item': FormItem,
    'el-input': Input,
    'el-checkbox': Checkbox,
    'el-button': Button,
  },
  data() {
    return {
      username: '',
      password: '',
      remember: false,
      loginError: {
        username: '',
        password: '',
      },
    }
  },
  methods: {
    ...mapActions([USER_LOGIN]),
    signup() {
      this.$router.push({ path: '/signup' })
    },
    login() {
      if (!this.username) {
        this.loginError.username = 'Username Required'
        return false
      } else if (!this.password) {
        this.loginError.password = 'Password Required'
        return false
      }
      const user = {
        username: this.username,
        password: this.password,
      }
      const config = {
        baseURL: '/api/v2',
        timeout: 10000,
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
      }
      axios.create(config).post('/auth', user).then((response) => {
        if (response.data.code === 0) {
          const password = user.password
          this.USER_LOGIN({ user: { ...user, password }, remember: this.remember })
          const path = decodeURIComponent(this.$route.query.redirect || '/')
          this.$router.push({ path })
        } else {
          this.loginError.username = 'Username Or Password Error'
          this.username = ''
          this.password = ''
        }
      })
    },
  },

}
</script>


<style lang="scss">
.login-view {
  background-color: #181818;
  height: 100%;
}
.login-view .login-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login-view .el-card {
  border: none;
  background-color: #fff;
}
.login-view .el-card__header {
  border-color: #EFF2F7;
  background-color: #fff;
}
.login-view .el-input.error input {
  border-color: #E0B4B4;
  border-width: 2px;
}
.login-view .el-button {
  padding: 9px 25px;
}
.login-view .login-footer {
  margin: 25px 0;
  text-align: right;
  border-bottom: 1px solid #EFF2F7;
  padding-bottom: 25px;
}
.login-view .checkbox {
  float: left;
  display: inline-block;
  margin: 6.5px 0 0;
}
.login-view .el-checkbox {
  .el-checkbox__inner:hover {
    border-color: #42d885;
  }
  .el-checkbox__input.is-checked .el-checkbox__inner{
    background-color: #42d885 !important;
    border-color: #42d885 !important;
  }
}
</style>



